/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  font-family: "Roboto", sans-serif;
  color: #000000;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Montserrat", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.5rem;
}

h2 {
  font-size: 3.1rem;
}

h3 {
  font-size: 2.8rem;
}

h4 {
  font-size: 2.3rem;
}

.section {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.content-center {
  max-width: 800px;
}

.align-right {
  display: flex;
  justify-content: flex-end;
}

.brown-button {
  background-color: #DDA15E;
  color: #000000;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  transition: all 0.1s;
  border-radius: 10px;
}
.brown-button:hover {
  background-color: #d48834;
}

.green-button {
  background-color: #36A543;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  transition: all 0.1s;
  border-radius: 10px;
}
.green-button:hover {
  background-color: #297f33;
}

.main {
  background-image: url("../../src/img/header2.jpg");
}

.logo {
  max-width: 160px;
  transition: all 0.2s ease-in-out;
}
.logo:hover {
  color: #DDA15E;
  border: 1px solid #DDA15E;
  padding: 5px;
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
  color: #ffffff;
}
.header.main {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.main {
    min-height: 50rem;
  }
}
@media (min-width: 768px) {
  .header.main .header-container {
    height: 45rem;
  }
}

.header-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header-container h1 {
  text-align: left;
  font-weight: 600;
  max-width: 50rem;
  line-height: 4rem;
}

.footer {
  background-color: #333333;
  padding: 2.5rem 0;
}
.footer .footer-container {
  padding: 2.5rem;
}
.footer .copyright {
  color: #ffffff;
  text-align: center;
  font-size: 1.5rem;
}

.bar {
  display: flex;
  flex-direction: column;
  padding-top: 2rem;
}
@media (min-width: 768px) {
  .bar {
    flex-direction: row;
    justify-content: space-between;
  }
}

@media (min-width: 768px) {
  .navbar {
    display: flex;
    align-items: center;
  }
}
.navbar a {
  color: #ffffff;
  text-decoration: none;
  font-size: 1.8rem;
  display: block;
  text-align: center;
  transition: color 0.2s;
}
.navbar a:hover {
  color: #DDA15E;
  border-bottom: 2px solid #DDA15E;
}
@media (min-width: 768px) {
  .navbar a {
    margin-right: 2rem;
  }
  .navbar a:last-of-type {
    margin-right: 0;
  }
}

@media (min-width: 768px) {
  .icons-aboutus {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icon {
  text-align: center;
}
.icon img {
  height: 7.5rem;
}
.icon h3 {
  text-transform: uppercase;
}
.icon p {
  font-size: 1.8rem;
}

@media (min-width: 768px) {
  .rent-container {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.rent {
  background-color: #ebebeb;
  border: 1px solid #333333;
  box-shadow: 5px 5px 10px #aeaeae;
  margin-top: 20px;
}
.rent .rent-container {
  padding: 2rem;
  gap: 0;
  display: flex;
  flex-direction: column;
  text-align: center;
}
.rent h4, .rent p {
  margin: 0;
}
.rent p {
  font-size: 1.8rem;
}
.rent .prize {
  color: #36A543;
  font-weight: 600;
  font-size: 2.5rem;
}

.icons-rent {
  display: flex;
  list-style: none;
  padding: 0;
}
.icons-rent li {
  flex: 1;
  display: flex;
}
.icons-rent li img {
  flex: 0 0 2rem;
  margin-right: 1rem;
}
.icons-rent li p {
  font-weight: 800;
  color: #000000;
}